/* Write your CSS here */
.ProjectCardContainer {
  width: 100%;
  height: 100%;
  margin-top: 7px;
  margin-bottom: 7px;
}

.projectImg {
  width: 100%;
  height: 100%;
}

.ProjectTitleAndDurationContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.projectTitle {
  color: #171f46;
  font-family: "Roboto";
  font-size: 20px;
  font-weight: 700;
  line-height: 1.3;
  margin: 0px;
}

.DurationContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Duration {
  color: #171f46;
  font-family: "Roboto";
  font-size: 12px;
  font-weight: 600;
  line-height: 1.2;
  margin-left: 4px;
}

.projectDescription {
  color: #1e293b;
  font-family: "Roboto";
  font-size: 12px;
  line-height: 1.5;
  margin-top: 0px;
}

.projectLink {
  text-align: center;
  color: #0967d2;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 600;
  line-height: 1.2;
  text-decoration: none;
  margin-top: 5px;
}
